
<template>
	<div id="upname">
		<van-nav-bar>
			<div slot="title">
				修改昵称
			</div>
			<van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
		</van-nav-bar>
		
		
		<div class="user" style="padding-top: 90px">
			<div class="help-input">
			  <img src="../../static/img/avatar-default.png"/>
			  <input type="text" placeholder="请输入新昵称" v-model="name">
			</div>
		</div>
		
		
		<div class="sub" @click="upname">确定</div>
		
	</div>
</template>

<script>
	 export default {
	  name: "upname",
	  data(){
	      return{
	        name:'',
	      }
	  },
	  methods:{
	      upname(){
	        if(this.name==''){
	          this.$toast('昵称不能为空');
	          return false;
	        }
			 let params = {
			  nickname: this.name,
			}
	        this.$POST('/user/updatename',params,true).then(res =>{
	          console.log(res);
	          if(res.code===200 ){
	           this.$router.push({path: '/my'});
	           this.$toast('修改成功');
	          }else {
	            this.$toast(res.msg);
	          }
	        }).catch(res=>{
	          this.$toast('连接服务器失败!');
	          console.log(res);
	        })
	      },
	  }
	}
     
	 
</script>

<style scoped>
  .user{
     width: 80%;
     margin: 0 auto;
     position: relative;
     top: -50px;
   }
  .user>img{
    width:90px;
    height:90px;
    border-radius:90px;
    margin: 0 auto;
    margin-bottom: 15px;
  }
 .help-input{
   text-align: left;
   margin-top: 20px;
   border-bottom: 1px solid #bfbfbf;
   padding-bottom: 10px;
   margin-bottom:25px;
 }
  .help-input>img{
    display: inline-block;
    width: 30px;
    position: relative;
    top:8px;
    margin-left: 5px;
  }
 .help-input>input{
   width:60%;
   border: 0;
   font-size: 18px;
   margin-left: 5px;
 }
  .sub{
    width: 80%;
    margin: 0 auto;
    border-radius: 30px;
    background: linear-gradient(to right, #5AB4FF , #5AB4FF,#5AB4FF);
    height: 45px;
    line-height: 45px;
    color: #ffffff;
    font-size: 18px;
    margin-top: -15px;
  }
  .dsf{
    width: 80%;
    margin: 0 auto;
    border-top: 1px solid #bfbfbf;
    margin-top: 50px;
  }
 .dsf>span{
   color: #959595;
   position: relative;
   top: -12px;
   display: block;
   background: #ffffff;
   width:100px;
   margin: 0 auto;
 }
 .dsf img{
   display: inline-block;
   width:55px;
   height:55px;
   border-radius:55px;
 }
 .dsf>div{
   width:40%;
   margin: 0 auto;
 }
 .dsf>div>img:first-child{
   float: left;
 }
 .dsf>div>img:last-child{
   float: right;
 }
 .help-input>span{
   float: right;
   line-height: 50px;
   color: #fe513f;
 }
</style>



